.container {
  width: 80%;
  background-color: #fff;
  height: 80vh;
  display: flex;
  position: relative;
  overflow: auto;
}
.left {
  height: 100%;
  width: fit-content;
  padding: 40px;
  flex-shrink: 0;
  position: sticky;
  top : 0;
  border-right: 1px solid #aaa;
}
.list{
  margin-bottom : 30px;
}
.list_btn {
  cursor: pointer;
  width: inherit;
  font-size: 16px;
}
.list_btn:hover {
  font-weight: 700;
  text-decoration: underline;
  text-underline-offset: 5px;
}
.right {
  flex-grow: 1;
  height: fit-content;
  min-height: 100%;
}
.close_btn {
  position: absolute;
  top: 20px;
  right: 20px;
  cursor: pointer;
  font-size: 24px;
}

@media (max-width : 800px){
  .container{
    display: block;
    width: 90%;
    height: 90vh;
  }
  .list{
    border-bottom : 1px solid #aaa;
  }
  .left {
    height : auto;
    width: 100%;
    padding: 10px 40px;
    position: static;
    border : none;
    margin-top : 50px;
  }
  .list_btn {
    cursor: pointer;
    width: inherit;
    font-size: 16px;
  }
  .list_btn:hover {
    font-weight: 700;
    text-decoration: none;
  }
}